  transform: translate3d(0, 0, 0); }

.swiperUp-enter-active {
  animation: swiperUp .6s; }

.swiperUp-leave-active {
  animation: swiperUp .6s reverse; }

@keyframes swiperUp {
  0% {
    transform: translateY(100%);
    opacity: 0; }
  100% {
    transform: translateY(0);
    opacity: 1; } }

.swiperLeft-enter-active {
  animation: swiperLeft .5s; }

.swiperLeft-leave-active {
  animation: swiperLeft .5s reverse; }

@keyframes swiperLeft {
  0% {
    opacity: 0;
    transform: translateX(-50%); }
  100% {
    opacity: 1;
    transform: translateX(0); } }

.fadeIn-enter-active {
  animation: fadeIn .4s; }

.fadeIn-leave-active {
  animation: fadeIn .4s reverse; }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes toBigHide {
  0% {
    transform: scale(0.1);
    opacity: 0; }
  50% {
    transform: scale(1.6);
    opacity: 1; }
  100% {
    transform: scale(4.6);
    opacity: 0; } }

