.markdown-view {
  color: #666666;
  word-wrap: break-word;
  font-size: 0.6rem;
  line-height: 1.68; }
  .markdown-view pre {
    margin: 10px 0 30px 0;
    padding: 10px;
    background: #333333;
    border-radius: 8px;
    overflow-x: auto;
    word-wrap: break-word; }
    .markdown-view pre::-webkit-scrollbar {
      width: 8px;
      height: 8px; }
    .markdown-view pre::-webkit-scrollbar-thumb {
      background-color: rgba(144, 146, 152, 0.5);
      border-radius: 6px; }
    .markdown-view pre::-webkit-scrollbar-track {
      background-color: rgba(0, 0, 0, 0); }
  .markdown-view li > code,
  .markdown-view p > code {
    max-width: 100%;
    padding: 0 8px;
    margin: 0 4px;
    background: rgba(35, 190, 213, 0.1);
    border-radius: 0.2rem; }
  .markdown-view blockquote,
  .markdown-view ol,
  .markdown-view ul,
  .markdown-view p,
  .markdown-view table,
  .markdown-view h1,
  .markdown-view h2,
  .markdown-view h3,
  .markdown-view h4 {
    margin-bottom: 0.6rem; }
  .markdown-view blockquote + h4,
  .markdown-view blockquote + h3,
  .markdown-view blockquote + h2,
  .markdown-view blockquote + h1,
  .markdown-view p + h4,
  .markdown-view p + h3,
  .markdown-view p + h2,
  .markdown-view p + h1,
  .markdown-view ol + h4,
  .markdown-view ol + h3,
  .markdown-view ol + h2,
  .markdown-view ol + h1,
  .markdown-view ul + h4,
  .markdown-view ul + h3,
  .markdown-view ul + h2,
  .markdown-view ul + h1,
  .markdown-view table + h4,
  .markdown-view table + h3,
  .markdown-view table + h2,
  .markdown-view table + h1 {
    margin-top: 0.9rem; }
  .markdown-view table thead tr {
    font-weight: 600; }
  .markdown-view a {
    color: #23bed5;
    transition: 0.2s ease-in; }
    .markdown-view a:hover {
      color: #de5585; }
  .markdown-view blockquote {
    position: relative;
    padding-left: 1rem;
    background: rgba(35, 190, 213, 0.1);
    padding: 0.25rem 0.5rem;
    border-left: 3px solid #23bed5; }
    .markdown-view blockquote p:last-of-type {
      margin-bottom: 0; }
    .markdown-view blockquote + ol,
    .markdown-view blockquote + ul {
      margin-top: 0.6rem; }
  .markdown-view ol {
    counter-reset: sectioncounter; }
    .markdown-view ol li {
      padding-left: 1rem;
      position: relative; }
      .markdown-view ol li:before {
        position: absolute;
        top: 0;
        left: 0;
        font-size: inherit;
        content: counter(sectioncounter) ".";
        counter-increment: sectioncounter;
        font-size: 0.6rem;
        color: #999999; }
  .markdown-view h1 {
    font-size: 1.2rem; }
  .markdown-view h2 {
    font-size: 1rem; }
  .markdown-view h3 {
    font-size: 0.8rem; }
  .markdown-view h4 {
    font-size: 0.6rem; }
  .markdown-view hr {
    margin: 1rem 0;
    border: none;
    height: 1px;
    background: linear-gradient(90deg, #23cca9, #23bed5);
    opacity: 0.2; }
  .markdown-view img {
    max-width: 100%;
    margin: 0.6rem auto; }
  .markdown-view em {
    font-style: normal;
    font-weight: 600;
    color: #de5585; }

