.nav {
  position: fixed;
  right: 0.75rem;
  top: 1rem;
}
.nav .nav-tap {
    display: block;
    width: 1.2rem;
    height: 1.2rem;
    border: 4px solid #fff;
    border-radius: 50%;
    cursor: pointer;
    position: relative;
    z-index: 99;
    right: 0.5rem;
}
.nav .nav-list {
    position: fixed;
    right: 0;
    top: 2.5rem;
    padding: 0 1rem 1rem 1rem;
    z-index: 98;
    text-align: center;
    -webkit-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out;
    opacity: 0;
}
.nav a {
    color: #fff;
    font-size: 0.6rem;
    -webkit-transition: 0.2s;
    transition: 0.2s;
    opacity: 0.6;
    position: relative;
}
.nav a:before {
      content: "";
      position: absolute;
      left: 0;
      bottom: -2px;
      width: 100%;
      height: 2px;
      background: #23bed5;
      -webkit-transition: -webkit-transform .5s ease-in-out;
      transition: -webkit-transform .5s ease-in-out;
      transition: transform .5s ease-in-out;
      transition: transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
      -webkit-transform: scaleX(0);
              transform: scaleX(0);
      -webkit-transform-origin: 100% 0;
              transform-origin: 100% 0;
}
.nav a:hover {
      opacity: 1;
}
.nav a:hover:before {
        -webkit-transform: scaleX(1);
                transform: scaleX(1);
        -webkit-transform-origin: 0 0;
                transform-origin: 0 0;
}
.nav:hover .nav-list {
    opacity: 1;
}

.sns-wrap {
  position: fixed;
  bottom: 1rem;
  left: 0.5rem;
  cursor: pointer;
}
.sns-wrap .sns-list {
    position: relative;
    padding-bottom: 2rem;
}
.sns-wrap .sns-list::after, .sns-wrap .sns-list::before {
      display: block;
      position: absolute;
      bottom: 0;
      left: 1.5rem;
      content: "";
      width: 4px;
      height: 1.5rem;
      background: #fff;
      -webkit-transition: 0.2s ease-in-out;
      transition: 0.2s ease-in-out;
}
.sns-wrap .sns-list::before {
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
}
.sns-wrap .sns-list::after {
      -webkit-transform: rotate(-45deg);
              transform: rotate(-45deg);
}
.sns-wrap .sns-item {
    -webkit-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out;
    opacity: 0;
    padding: 0.25rem;
    border: 1px solid #fff;
    font-size: 0.5rem;
    border-radius: 1rem;
    text-align: center;
    font-size: 0.6rem;
}
.sns-wrap .sns-item + .sns-item {
      margin-top: 20px;
